@import url('https://fonts.googleapis.com/css2?family=Saira:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;1,200&display=swap');

body {
  background-color: #111111;
}

* {
  font-family: 'Saira', sans-serif !important;
}

.hero {
  background-image: url(https://shido.dev/portfolio/assets/header.webp);
  background-size: cover;
}

::-webkit-scrollbar-thumb {
  background: #cccccccc;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

::-webkit-scrollbar {
  width: 12px;
  background: #111111;
}